import { Component, OnInit } from '@angular/core';
import { ProductsService } from 'src/app/services/products.service';
import { Product } from 'src/app/models/product';
import { Router } from '@angular/router';
import { Category } from 'src/app/models/category';
import { HttpClient } from '@angular/common/http';



@Component({
  selector: 'app-add-product',
  templateUrl: './add-product.component.html',
  styleUrls: ['./add-product.component.css']
})
export class AddProductComponent implements OnInit {

    public product = new Product;
    public fd = new FormData();
    public categories: Category[];
    public fileSelected: File = null;
    public pName: string;
    public price: any;
    public category: any;
    public error: string;
    

      

    public constructor(private http: HttpClient, private productsService: ProductsService, private router: Router) { }  

       
    ngOnInit():void {
        const observableCategory = this.productsService.getAllCategories();
        observableCategory.subscribe(category =>  this.categories = category 
             , err =>{
          alert(err);
        });
    }

    public onFileSelected(event){
        this.fileSelected = event.target.files[0];
    }

    public upload(){
        this.fd.append("pName", this.pName);
        this.fd.append("price", this.price);
        this.fd.append("category", this.category);        
        this.fd.append('img', this.fileSelected, this.fileSelected.name);
        this.http.post("http://localhost:3000/api/products", this.fd).subscribe(res=>{
            console.log(res);
            alert("המוצר הועלה בהצלחה");
        },err => {
            console.log(err);
        });
        
    }

       
    
}
