import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ProductsService } from 'src/app/services/products.service';
import { CartsService } from 'src/app/services/carts.service';
import { AuthService } from 'src/app/services/auth.service';
import { Cart } from 'src/app/models/cart';
import { Product } from 'src/app/models/product';
import { CartProducts } from 'src/app/models/cartProducts';
import { User } from 'src/app/models/user';
import { Order } from 'src/app/models/order';
import { OrdersService } from 'src/app/services/orders.service';
import { City } from 'src/app/models/city';

@Component({
  selector: 'app-orders',
  templateUrl: './orders.component.html',
  styleUrls: ['./orders.component.css']
})
export class OrdersComponent implements OnInit {
    
    public newOrder = new Order();
    public userName: string;
    public userCity: string;
    public userAddress: string;    
    public orderError: string;
    public order: Order;    
    public email = localStorage.getItem("email");
    public cartTotalPrice: any;
    public date = new Date();    
    public thisCart: Cart;
    private cartProducts: CartProducts[];    
    public cartProductsLength: any;
    public product: Product;
    public products: Product[] = new Array();
    public i: number;
    public totalPrice: any;
    public sum: any = 0;
    public user: User;
    public minFormDate: any;        
    public maxFormDate: any;
    public showModal: boolean = false; 
    public cities: City[];       

  constructor(
    private _router: Router,
    private productsService: ProductsService,
    private cartsService: CartsService,
    private _authService: AuthService,
    private ordersService: OrdersService
  ) { }

  ngOnInit() {

            // get dates for order form

            this.minFormDate = this.date.getFullYear() + '-' + ('0' + (this.date.getMonth() + 1)).slice(-2) + '-' + ('0' + this.date.getDate()).slice(-2);
            this.maxFormDate = this.date.getFullYear() + '-' + ('0' + (this.date.getMonth() + 1)).slice(-2) + '-' + ('0' + (this.date.getDate() + 5)).slice(-2);
            

            // get all cities

            const observableCity = this.productsService.getAllCities();
            observableCity.subscribe(city => this.cities = city);

           // get user

    const userObservable = this._authService.getUser(this.email);
    userObservable.subscribe(user => {            
        this.user = user;
        this.userName = user.fName + " " + user.lName;
        this.userCity = user.city;
        this.userAddress = user.address + " " + user.houseNumber;
        
        // get user cart: 
       const cartObservable = this.cartsService.getLastCart(user._id);
       cartObservable.subscribe(thisCart =>{
        this.thisCart = thisCart

        // Get cart products         
    
    const cartProductsObservable = this.cartsService.getAllCartProducts(thisCart._id);
    cartProductsObservable.subscribe(cartProducts =>{
    this.cartProducts = cartProducts;
    this.cartProductsLength = cartProducts.length;
    for(this.i = 0; this.i <= this.cartProductsLength - 1; this.i++){
        this.getProductInfo(this.cartProducts[this.i].product);                                 
    }
    
    this.getTotalPrice();      
         });         
       });
    });

    
                    
           
       }

        
       
       
       public getProductInfo(id){
           const productObservable = this.productsService.getOneProduct(id);
           productObservable.subscribe(product =>{
               this.products.push(product);            
           });
           
       }
   
      
       public getTotalPrice(){
            
        for(this.i = 0; this.i < this.cartProductsLength; this.i++){
            let totalPriceOfProduct = this.cartProducts[this.i].totalPrice;
            this.sum = this.sum + totalPriceOfProduct;
            
        }       
        
    }

    
    public createNewCart(){  
        const userId = this.user._id;
        const date = new Date();
        const cart = new Cart(userId, date);  
        this.cartsService.createNewCart(cart)    
                .subscribe( thisCart => {
                    this.thisCart = thisCart
                    localStorage.setItem("cart", thisCart._id);                    
                }, response => alert("Error: " + response.error.message));
        }    
        
        
    
    
    


    public createNewOrder(name, city, address, date, creditCartNumber){  
        
            const theOrder = new Order(this.user, this.thisCart, this.sum, name, city, address, date, creditCartNumber);
            const orderObservable = this.ordersService.addNewOrder(theOrder);
            orderObservable.subscribe(order=>{
                this.showModal = true;
                this.order = order;
                // const observable = this.cartsService.deleteCart(this.thisCart._id);
                // observable.subscribe(()=>{
                this.createNewCart();
                  setTimeout(()=>{
                    this._router.navigate(["/"]);
                  },30000);                      
                // });
                    
                },err => this.orderError = err);              
                           
            
      }

      public returnToLobby(){
        this.showModal = false;
        this._router.navigate(["/"]);
      }
      
        
   

}
