import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, observable } from 'rxjs';
import { Product } from '../models/product';
import { Category } from '../models/category';
import { City } from '../models/city';

@Injectable({
  providedIn: 'root'
})
export class ProductsService {
    
    private _apiURL = "http://localhost:3000/";
    private _productsUrl = "http://localhost:3000/api/products";
    private _productsLengthUrl = "http://localhost:3000/api/products/length";
    private _categoriesUrl = "http://localhost:3000/api/categories";
    private _citiesUrl = "http://localhost:3000/api/cities";
    


    constructor(private http: HttpClient) { }


//   Products

  public getProductsLength(){
    return this.http.get(this._productsLengthUrl);
  }

  public getProductByCategory(category) {
    const categoryUrl = this._productsUrl + "/category/" + category;
    return this.http.get<Product[]>(categoryUrl);
  }

   public getAllProducts(): Observable<Product[]>{
      return this.http.get<Product[]>(this._productsUrl);
  }

  public getOneProduct(id: string): Observable<Product>{
    return this.http.get<Product>(this._productsUrl + "/" + id);
}

  public addProduct(product: Product): Observable<Product>{
      return this.http.post<Product>(this._productsUrl, product);
  }

  public updateProduct(id: string, product: FormData): Observable<Product>{
    return this.http.patch<Product>(this._productsUrl + "/" + id, product);
}

  public deleteProduct(id: Product["_id"]): Observable<Product["_id"]> {
    return this.http.delete<Product["_id"]>(this._productsUrl + "/" + id);
}

  public getAllCategories() : Observable<Category[]> {
    return this.http.get<Category[]>(this._categoriesUrl);
}

  public getAllCities() : Observable<City[]> {
    return this.http.get<City[]>(this._citiesUrl);
}

  public getFilterProduct(name: Product["pName"]): Observable<Product>{
    return this.http.get<Product>(this._productsUrl + "/" + name);
  }



  

}
