import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Cart } from '../models/cart';
import { Observable } from 'rxjs';
import { CartProducts } from '../models/cartProducts';


@Injectable({
  providedIn: 'root'
})
export class CartsService {


    public cartProducts: any;

  
  private _cartsUrl = "http://localhost:3000/api/carts";
  private _cartsOfUserUrl = "http://localhost:3000/api/carts/user";
  private _cartProductUrl = "http://localhost:3000/api/cartProducts";
  private _lastCartUrl = "http://localhost:3000/api/carts/user/lastCart";

  

  constructor(private http: HttpClient) { }

//   cart

  public getAllCartsOfUser(userId: string): Observable<Cart[]>{
    return this.http.get<Cart[]>(this._cartsOfUserUrl + "/" + userId);
  }

  public createNewCart(cart: Cart): Observable<Cart>{
    return this.http.post<Cart>(this._cartsUrl, cart);    
  }

  public addNewCartProduct(cartProduct: CartProducts): Observable<CartProducts>{
     return this.http.post<CartProducts>(this._cartProductUrl, cartProduct);     
}

  public getAllCartProducts(cartId: any): Observable<CartProducts[]>{
    return this.http.get<CartProducts[]>(this._cartProductUrl + "/" + cartId);
  }

  public getOneCart(id: string): Observable<Cart>{
    return this.http.get<Cart>(this._cartsUrl + "/" + id);
}

public getLastCart(id: string): Observable<Cart>{
    return this.http.get<Cart>(this._lastCartUrl + "/" + id);
}

public getCartProductsLength(){
    return this.http.get(this._cartProductUrl + "/length");
  }

  public deleteCartProduct(id: CartProducts["_id"]): Observable<CartProducts["_id"]> {
    return this.http.delete<CartProducts["_id"]>(this._cartProductUrl + "/" + id);
}

public deleteCart(id: string): Observable<Cart> {
    return this.http.delete<Cart>(this._cartsUrl + "/" + id);
}

}
