import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { User } from '../models/user';
import { Observable, config } from 'rxjs';



@Injectable()

export class AuthService {

    private _registerUrl = "http://localhost:3000/api/register";
    private _userUrl = "http://localhost:3000/api/users/";
    private _loginUrl = "http://localhost:3000/api/login";
    private _loginAdminUrl = "http://localhost:3000/api/loginAdmin";
    

    public constructor(private http: HttpClient, private _router: Router) { }

    public getUser(email){
        return this.http.get<any>(this._userUrl + email);
    }

    public getAllusers(): Observable<User[]>{
        return this.http.get<User[]>(this._userUrl);
    }

    public register(user){
        return this.http.post<any>(this._registerUrl, user);
    }

    public login(user){
        return this.http.post<any>(this._loginUrl, user);
    }

    public loginAdmin(admin){
        return this.http.post<any>(this._loginAdminUrl, admin);
    }

    public loggedIn(){
        return !!localStorage.getItem('token');
    }

    public loggedInAdmin(){
        return !!localStorage.getItem('tokenOfAdmin');
    }

    public logoutUser(){
        localStorage.removeItem('token');
        localStorage.removeItem('email');
        this._router.navigate(['/']);
    }

    public logoutAdmin(){
        localStorage.removeItem('tokenOfAdmin');
        this._router.navigate(['/']);
    }

    public getToken(){
        return localStorage.getItem('token');
    }

    public getTokenOfAdmin(){
        return localStorage.getItem('tokenOfAdmin');
    }
}
