import { Component, OnInit } from '@angular/core';
import { OrdersService } from 'src/app/services/orders.service';
import { Order } from 'src/app/models/order';
import { CartsService } from 'src/app/services/carts.service';


@Component({
  selector: 'app-admin-orders',
  templateUrl: './adminOrders.component.html',
  styleUrls: ['./adminOrders.component.css']
})
export class AdminOrdersComponent implements OnInit {

    public orders: Order[];
    public ordersCount: any;

  constructor(private ordersService: OrdersService, private cartsService: CartsService) { }

  ngOnInit() {

    const ordersObservable = this.ordersService.getAllorders();
    ordersObservable.subscribe(orders=>{
        this.orders = orders;
        this.ordersCount = orders.length;
    })
  }

}
