import { Component, OnInit } from '@angular/core';
import { AuthService } from 'src/app/services/auth.service';
import { Router } from '@angular/router';
import { User } from 'src/app/models/user';
import { City } from 'src/app/models/city';
import { ProductsService } from 'src/app/services/products.service';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

    public registerUserData = new User();
    public cities: City[];
    public emailExist: String; 
    


  constructor(private _auth: AuthService, private _router: Router, private productsService: ProductsService) { }

  public ngOnInit(): void {
    const observableCity = this.productsService.getAllCities();
        observableCity.subscribe(city => this.cities = city , err =>{
          alert(err);
        });
}

   validatePasswordChecker() {
    return !this.registerUserData.passwordChecker || this.registerUserData.password === this.registerUserData.passwordChecker;
  }

  registerUser() {

    this._auth.register(this.registerUserData)
    .subscribe(
     res => {
          localStorage.setItem('token', res.token);          
          localStorage.setItem('email', this.registerUserData.email);          
          this._router.navigate(['']);
    },        
     err => { console.log(err);
     const emailExist = err.error;
     this.emailExist = emailExist;
     }
    )
  }

}
