import { Component, OnInit } from '@angular/core';
import { Product } from 'src/app/models/product';
import { Category } from 'src/app/models/category';
import { ProductsService } from 'src/app/services/products.service';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';


@Component({
  selector: 'app-products-control',
  templateUrl: './products-control.component.html',
  styleUrls: ['./products-control.component.css']
})
export class ProductsControlComponent implements OnInit {

    public product = new Product;
    public fd = new FormData();
    public products: Product[];  
    public categories: Category[];
    public today = new Date();
    public productsCount: any;    
    public showModal: boolean = false;
    public productId: String;
    public productName: any;   
    public productPrice: any;
    public productCategory: any;
    public productImage: any;

    public fileSelected: File = null;   
    
    public constructor(private productsService: ProductsService,
      private http: HttpClient,  
      private _router: Router) { }
  
    public ngOnInit(): void {

        this.productsService.getProductsLength().subscribe(productsCount => this.productsCount = productsCount);
  
        
        const observable = this.productsService.getAllProducts();
        observable.subscribe(products => {
          this.products = products;
        });  
  
        const observableCategory = this.productsService.getAllCategories();
            observableCategory.subscribe(category => this.categories = category , err =>{
              alert(err);
            });
    } 

    public getProduct(id){
        this.showModal = true;
        const productObservable = this.productsService.getOneProduct(id);
        productObservable.subscribe( product =>{
            this.productId = product._id;
            this.productName = product.pName;           
            this.productPrice = product.price;           
            this.productCategory = product.category;
            this.productImage = product.img;
            
        })
    }

    public onFileSelected(event){
        this.fileSelected = event.target.files[0];
    }

    public updateProduct(name,price,category){

         this.product = {pName: name, price: price, category: category};
         
         
         this.http.patch("http://localhost:3000/api/products/" + this.productId, this.product).subscribe(update=>{
                const observable = this.productsService.getAllProducts();
                observable.subscribe(products => {
                this.products = products;
                });
                
            alert("המוצר עודכן בהצלחה");
            this.showModal = false;
         })        
    }

    public updateProductImage(){

        this.fd.append('img', this.fileSelected, this.fileSelected.name);
        
        this.http.patch("http://localhost:3000/api/products/changeImage/" + this.productId, this.fd).subscribe(update=>{
               const observable = this.productsService.getAllProducts();
               observable.subscribe(products => {
               this.products = products;
               });
               
           alert("תמונה עודכנה בהצלחה");
           this.showModal = false;
        })        
   }



    

      
  
  }
