import { Component, OnInit } from '@angular/core';
import { OrdersService } from 'src/app/services/orders.service';
import { ProductsService } from 'src/app/services/products.service';

@Component({
  selector: 'app-admin-tab',
  templateUrl: './admin-tab.component.html',
  styleUrls: ['./admin-tab.component.css']
})
export class AdminTabComponent implements OnInit {

   public ordersCount: any;
   

  constructor(private orserService: OrdersService, private productsService: ProductsService, private ordersService: OrdersService) { }

  ngOnInit() {
    
    this.ordersService.getOrdersLength().subscribe(ordersCount => this.ordersCount = ordersCount);
  }

}
