import { Component, OnInit } from '@angular/core';
import { CartProducts } from '../../models/cartProducts';
import { Router } from '@angular/router';
import { ProductsService } from 'src/app/services/products.service';
import { CartsService } from 'src/app/services/carts.service';
import { Cart } from 'src/app/models/cart';
import { Product } from 'src/app/models/product';
import { User } from 'src/app/models/user';
import { AuthService } from 'src/app/services/auth.service';

@Component({
  selector: 'app-user-cart',
  templateUrl: './user-cart.component.html',
  styleUrls: ['./user-cart.component.css']
})
export class UserCartComponent implements OnInit {

    public email = localStorage.getItem("email");
    public cartId = localStorage.getItem("cart");

    private cartProducts: CartProducts[];    
    public cartProductsLength: any;
    public product: Product;
    public products: Product[] = new Array();
    private total: number = 0;
    private thisCart: Cart;
    public i: number;
    public totalPrice: any;
    public sum: any = 0;
    public user: User;
    
    
    



	constructor(
		private _router: Router,
        private productsService: ProductsService,
        private cartsService: CartsService,
        private _authService: AuthService
        

	) { }

	ngOnInit() {

        // Get user Information

        
        const userObservable = this._authService.getUser(this.email);
        userObservable.subscribe(user => this.user = user);

        // Get user Cart

            
            const cartObservable = this.cartsService.getOneCart(this.cartId);
            cartObservable.subscribe(thisCart => this.thisCart = thisCart);

        // Get user Products         
        
           this.getUserProducts(this.cartId);

        // Get length of the cart products         
        
            const cartProductsLengthObservable = this.cartsService.getCartProductsLength();
            cartProductsLengthObservable.subscribe(cartProductsLength => this.cartProductsLength = cartProductsLength);
            
            setTimeout(()=>{
                for(this.i = 0; this.i < this.cartProductsLength; this.i++){
                    this.getProductInfo(this.cartProducts[this.i].product);                    
                }                
            },1000);

            setTimeout(()=>{
            this.getTotalPrice();
            },1500);
		
    }

    
    
    public getProductInfo(id){
        const productObservable = this.productsService.getOneProduct(id);
        productObservable.subscribe(product =>{
            this.products.push(product);            
        });
        
    }

    public getUserProducts(cartId){
        const cartProductsObservable = this.cartsService.getAllCartProducts(cartId);
            cartProductsObservable.subscribe(cartProducts =>{
                this.cartProducts = cartProducts                 
            });
    }

    public getTotalPrice(){
        
        for(this.i = 0; this.i < this.cartProductsLength; this.i++){
            let totalPriceOfProduct = this.cartProducts[this.i].totalPrice;
            this.sum = this.sum + totalPriceOfProduct;
        }       
        
    }

    public deletecartProduct(id: string): void {
        const observable = this.cartsService.deleteCartProduct(id);
        observable.subscribe(() => {
            alert("המוצר נמחק בהצלחה");                       
        }, response => {
            alert("Error: " + response.error.message);

        });
    }

    public deleteCart(id: string): void {
        const observable = this.cartsService.deleteCart(id);
        observable.subscribe(() => {
            this.createNewCart();
            alert("העגלה אופסה בהצלחה");            
        }, response => {
            alert("Error: " + response.error.message);

        });
    }

    public createNewCart(){  
        const userId = this.user._id;
        const date = new Date();
        const cart = new Cart(userId, date);  
        this.cartsService.createNewCart(cart)    
                .subscribe( thisCart => {
                    this.thisCart = thisCart
                    localStorage.setItem("cart", this.thisCart._id);                    
                }, response => alert("Error: " + response.error.message));
        }


    public submitOrder(){
        this._router.navigate(["/orders"]);                
    }


    
    

}
