import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './users/login/login.component';
import { RegisterComponent } from './users/register/register.component';
import { HomeComponent } from './users/home/home.component';
import { AuthGuard } from './auth.guard';
import { LobbyComponent } from './users/lobby/lobby.component';
import { Error404Component } from './components/error404/error404.component';

import { AdminTabComponent } from './admin/admin-tab/admin-tab.component';
import { UserComponent } from './users/user/user.component';
import { UserCartComponent } from './users/user-cart/user-cart.component';
import { AdminLoginComponent } from './admin/admin-login/admin-login.component';
import { AuthAdminGuard } from './auth-admin.guard';
import { OrdersComponent } from './users/orders/orders.component';
import { AdminOrdersComponent } from './admin/admin-orders/adminOrders.component';
import { UsersComponent } from './admin/users/users.component';


const routes: Routes = [
    {path: '', component: LobbyComponent},
    {path: 'home', canActivate: [AuthGuard], component: HomeComponent},    
    {path: 'admin', canActivate: [AuthAdminGuard], component: AdminTabComponent},
    {path: 'admin-login', component: AdminLoginComponent},
    {path: 'admin-orders', canActivate: [AuthAdminGuard], component: AdminOrdersComponent},
    {path: 'users', canActivate: [AuthAdminGuard], component: UsersComponent},
    {path: 'login', component: LoginComponent},
    {path: 'user-cart', component: UserCartComponent},
    {path: 'user', component: UserComponent},
    {path: 'register', component: RegisterComponent},
    {path: 'orders', component: OrdersComponent},
    {path: 'Error404', component: Error404Component},
    {path: '**', redirectTo: "/Error404", pathMatch: "full"}
    
    
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
