import { Component, OnInit } from '@angular/core';
import { Product } from '../../models/product';
import { Category } from '../../models/category';
import { ProductsService } from '../../services/products.service';
import { Router } from '@angular/router';
import { CartsService } from '../../services/carts.service';
import { CartProducts } from '../../models/cartProducts';
import { Cart } from '../../models/cart';
import { User } from '../../models/user';
import { AuthService} from '../../services/auth.service';


@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.css']
})
export class ProductsComponent implements OnInit {

    public email = localStorage.getItem("email");

  

    private cartProducts: CartProducts[];    
    public cartProductsLength: any;
    public product: Product;
    public arrayOfProducts: Product[] = new Array();
    private total: number = 0;
    private thisCart: Cart;
    public i: any;
    public totalPrice: any;
    public sum: any = 0;
    public user: User;





  public toggle: boolean = true;
  public savedChanges = false;
  public error: boolean = false;
  public errorMessage: String = "";
  public dataLoading: boolean = false;     
  public products: Product[];   
  public filterProducts: Product[];
  public categories: Category[];  
  public counter: number = 1;
  private allProducts: Product[];
  private meatProducts: Product[];
  private milkProducts: Product[];
  private beverageProducts: Product[];
  private vegetablesProducts: Product[];
  private thisProduct: any = '';
  private categoryMilk: string = "5cab1e10e5f6c63f0ce1c486";
  private categoryMeat: string = "5cab1e9eeef5313f0c1e44c8";
  private categoryBeverage: string = "5cab1ec1eef5313f0c1e44c9";
  private categoryVegetables: string = "5cab1e6ceef5313f0c1e44c7";
  private cart: Cart; 
  public showModal: boolean = true;
  
  

     
  
  public constructor(public _authService: AuthService, public productsService: ProductsService, public cartsService: CartsService,
    private _router: Router) { }

  public ngOnInit() {

    

            // get user

    const userObservable = this._authService.getUser(this.email);
        userObservable.subscribe(user => {            
            this.user = user;     

            // get user cart: 
           const cartObservable = this.cartsService.getLastCart(user._id);
           cartObservable.subscribe(cart =>{
            this.cart = cart

            // Get cart products         
        
        const cartProductsObservable = this.cartsService.getAllCartProducts(cart._id);
        cartProductsObservable.subscribe(cartProducts =>{
        this.cartProducts = cartProducts;
        this.cartProductsLength = cartProducts.length;
        for(this.i = 0; this.i < this.cartProductsLength; this.i++){
            this.getProductInfo(this.cartProducts[this.i].product);                                 
        }
        this.getTotalPrice();         
      }); 
           });
        });

        
    // Get All Products

      const observable = this.productsService.getAllProducts();
      observable.subscribe(products => {
        this.products = products;
        this.allProducts = products;
      });      
      
      // Milk Category

        const milkObservable = this.productsService.getProductByCategory(this.categoryMilk);
        milkObservable.subscribe(milkProducts => this.milkProducts = milkProducts);

      // Meat Category

      const meatObservable = this.productsService.getProductByCategory(this.categoryMeat);
      meatObservable.subscribe(meatProducts => this.meatProducts = meatProducts);

      // Beverage Category

      const beverageObservable = this.productsService.getProductByCategory(this.categoryBeverage);
      beverageObservable.subscribe(beverageProducts => this.beverageProducts = beverageProducts);

     // Beverage Category

      const vegetablesObservable = this.productsService.getProductByCategory(this.categoryVegetables);
      vegetablesObservable.subscribe(vegetablesProducts => this.vegetablesProducts = vegetablesProducts);

          

  }

  // Filter Products functions  

  getAllProducts(){
    this.products = this.allProducts;
  }

  categoryIsMilk(){    
    this.products = this.milkProducts;    
  }

  categoryIsMeat(){     
    this.products = this.meatProducts;    
  }

  categoryIsBeverage(){    
    this.products = this.beverageProducts;    
}

  categoryIsVegetables(){    
    this.products = this.vegetablesProducts;    
}

  getProductDetails(id){
    this.showModal = true;
    const productObservable = this.productsService.getOneProduct(id);
    productObservable.subscribe(thisProduct => this.thisProduct = thisProduct);    
  }

  countProd(filter){
      if(filter == "add"){
          this.counter = this.counter + 1;
      } else {
          if(this.counter > 0 ) { 
              this.counter = this.counter - 1;
          }
      }
  }

  
  
  addToCart(product,amount,productPrice){
    const totalPrice = productPrice*amount;        
    const newCartProduct = new CartProducts(product, amount, totalPrice, this.cart);
    this.cartsService.addNewCartProduct(newCartProduct)
    .subscribe(newProduct => {        
        this.counter = 1;
        this.showModal = false;        
        this.totalPriceAfterAddProduct(totalPrice);
        this.getProductInfo(newProduct.product);
        this.cartProducts.push(newProduct);               
        }, response => alert("Error: " + response.error.message));
  }

  

  public getProductInfo(id){
    const productObservable = this.productsService.getOneProduct(id);
    productObservable.subscribe(product =>{
        this.arrayOfProducts.push(product);            
    });
    
}

public getTotalPrice(){
    
    for(this.i = 0; this.i < this.cartProductsLength; this.i++){
        let totalPriceOfProduct = this.cartProducts[this.i].totalPrice;
        this.sum = this.sum + totalPriceOfProduct;
    }       
    
}

public totalPriceAfterAddProduct(price){
    this.sum = this.sum + price;
}

public totalPriceAfterDelete(price){
    this.sum = this.sum - price;
}

public deletecartProduct(id: string, row, price): void {
    const observable = this.cartsService.deleteCartProduct(id);
    observable.subscribe(deletedProduct => {
        console.log(id);                
        console.log(deletedProduct);                
        document.getElementById(row).remove();
        this.totalPriceAfterDelete(price);        
    }, response => {
        alert("Error: " + response.error.message);
    });
}

public deleteCart(): void {
    console.log(this.cart._id);
    const observable = this.cartsService.deleteCart(this.cart._id);
    observable.subscribe(()=>{
        this.createNewCart();       
        for(this.i = 0; this.i <= this.arrayOfProducts.length; this.i++){        
            this.arrayOfProducts.splice(this.i,this.arrayOfProducts.length);          
            this.cartProducts.splice(this.i,this.cartProducts.length);          
            this.sum = 0;          
        }    
    })
    
}

public createNewCart(){  
    const userId = this.user._id;
    const date = new Date();
    const cart = new Cart(userId, date);  
    this.cartsService.createNewCart(cart)    
            .subscribe( thisCart => {
                this.cart = thisCart
                localStorage.setItem("cart", thisCart._id);                    
            }, response => alert("Error: " + response.error.message));
    }


public submitOrder(){
    this._router.navigate(["/orders"]);                
}

public continueShopping(){
let cartDiv = document.getElementById("userCart");
cartDiv.className = "hide";
}


public showCart(){
    let cartDiv = document.getElementById("userCart");
    cartDiv.className = "cart";
}

  

  
}
