import { Component, OnInit } from '@angular/core';
import { AuthService } from 'src/app/services/auth.service';
import { User } from 'src/app/models/user';
import { CartsService } from 'src/app/services/carts.service';
import { Cart } from 'src/app/models/cart';
import { Router } from '@angular/router';
import { ProductsService } from 'src/app/services/products.service';
import { OrdersService } from 'src/app/services/orders.service';
import { CartProducts } from 'src/app/models/cartProducts';
import { Order } from 'src/app/models/order';



@Component({
  selector: 'app-lobby',
  templateUrl: './lobby.component.html',
  styleUrls: ['./lobby.component.css']
})
export class LobbyComponent implements OnInit {

  public email: string;
  public user: User;
  public newCart: Cart;
  public cart: Cart;
  public userCarts: Cart[];
  public thisCart: Cart;
  public lastCart: Cart;
  public loaderDone: boolean = false;
  public productsCount: any;
  public ordersCount: any;
  public cartProducts: CartProducts[];
  public sum: any = 0;
  public lastCartTotalPrice: any = 0;
  public i: number;
  public welcomeMessage: boolean = false;
  public lastOrder: any;
  public lastOrderCart: any;
  
  

  constructor(public _authService: AuthService, public ordersService: OrdersService, public productsService: ProductsService, public _router: Router, public cartServices: CartsService) { }

  ngOnInit() {

    this.productsService.getProductsLength().subscribe(productsCount => this.productsCount = productsCount);
    this.ordersService.getOrdersLength().subscribe(ordersCount => this.ordersCount = ordersCount);
    

    // Get User
    this.email = localStorage.getItem("email");
    const userObservable = this._authService.getUser(this.email);
    userObservable.subscribe(user => {
        this.user = user;

        //  Get all carts of User
        const allCartsObservable = this.cartServices.getAllCartsOfUser(user._id);
        allCartsObservable.subscribe(userCarts =>{
            this.userCarts = userCarts            
            if(this.userCarts.length === 0){
                this.welcomeMessage = true;
            }
            // Get Last Cart
            const lastCartObservable = this.cartServices.getLastCart(this.user._id);
        lastCartObservable.subscribe(cart => {
            this.cart = cart;
            
            const cartProductsObservable = this.cartServices.getAllCartProducts(cart._id);
            cartProductsObservable.subscribe(cartProducts => {
                this.cartProducts = cartProducts;
                if(!(this.cartProducts.length == 0)){
                for(this.i = 0; this.i < this.cartProducts.length; this.i++){                    
                    this.sum = this.sum + this.cartProducts[this.i].totalPrice;
                    this.lastCartTotalPrice = this.sum;
                }
            }

            const userLastOrderObservable = this.ordersService.getLastOrder(this.user._id);
            userLastOrderObservable.subscribe(lastOrder=>{
                this.lastOrder = lastOrder;
                
                const cartInObservable = this.cartServices.getOneCart(this.lastOrder.cart);
                cartInObservable.subscribe(lastOrderCart=>{
                    this.lastOrderCart = lastOrderCart;
                    
                })
            })
                         
            })
       
        });


        });
        
    });

     

    
  }

  
  public createNewCart(){  
    const userId = this.user._id;
    const date = new Date();
    const cart = new Cart(userId, date);  
    this.cartServices.createNewCart(cart)    
            .subscribe( thisCart => {
                this.thisCart = thisCart
                // localStorage.setItem("cart", this.thisCart._id);
                this._router.navigate(["/home"]);                
            }, response => alert("Error: " + response.error.message));
    }

    
    
    public useCurrentCart(){
        this._router.navigate(["/home"]);                
    }
      
      
  }

  

  





