import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Order } from '../models/order';

@Injectable({
  providedIn: 'root'
})
export class OrdersService {

    private _apiOrders = "http://localhost:3000/api/orders/";
    private _ordersUrl = "http://localhost:3000/api/orders/addNew";
    private _ordersLengthUrl = "http://localhost:3000/api/orders/length";
    private _userOrdersUrl = "http://localhost:3000/api/orders/user";
    private _lastOrderUrl = "http://localhost:3000/api/orders/user/lastOrder";

  constructor(private http: HttpClient) { }

  //   Orders  

public getAllorders(): Observable<Order[]>{
    return this.http.get<Order[]>(this._apiOrders);
}

public getLastOrder(id: string): Observable<Order>{
    return this.http.get<Order>(this._lastOrderUrl + "/" + id);
}

public getOrdersLength(){
    return this.http.get(this._ordersLengthUrl);
}

public getAllUserOrders(id: string): Observable<Order[]>{
    return this.http.get<Order[]>(this._userOrdersUrl + "/" + id);
}

public addNewOrder (order: Order): Observable<Order>{
    return this.http.post<Order>(this._ordersUrl, order);
}


}
